<template>
<div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon"></i>
                    <a href="#"></a>
                </li>
                <li class="active"><router-link to="/navs">微服务</router-link></li>
                <li class="active">实名认证</li>
            </ul>
        </div>
    <div class="page-content ha_page_content" :style="mianStyle">
        <div class="el-row contBox clear">
            <div :class="[onlyOneClass, is_show_rightbox ? twoClass : '']">

                <div class="cs-filter-box">
                    <!-- 筛选 -->
                    <div class="cs-export-box" v-if="false">
                        <el-button :plain="true" type="primary">导出</el-button>
                    </div>
                    <el-button-group>
                        <el-button :plain="flag != 'authing'" type="primary" @click="filterTag('authing')">未审核</el-button>
                        <el-button :plain="flag != 'authed,authFail'" type="primary" @click="filterTag('authed,authFail')">已审核</el-button>
                    </el-button-group>

                    <div class="cs-searchBox ">
                        <input type="text" v-model="keyword" placeholder="姓名/联系电话" autocomplete="off" class="el-input__inner cs-search_input" @keyup.enter="search">
                        <img src="../../img/lookup.png" @click="search" alt="搜索">
                    </div>
                </div>

                <div class="cs-list-table"><!-- 列表 -->
                    <el-table :data="tableData" border style="width: 100%" @row-click="handleRowHandle">
                        <el-table-column
                                type="index"
                                label="序号"
                                width="65">
                        </el-table-column>

                        <el-table-column
                                label="单位类型"
                                width="100">
                            <template scope="scope">{{ scope.row.deptInfo.type}}</template>
                        </el-table-column>

                        <el-table-column
                                label="单位">
                            <template scope="scope">{{ scope.row.deptInfo.name}}</template>
                        </el-table-column>

                        <el-table-column
                                prop="realname"
                                label="姓名"
                                width="180">
                        </el-table-column>

                        <el-table-column
                                prop="cellphone"
                                label="联系电话"
                                width="180">
                        </el-table-column>

                        <el-table-column
                                width="200"
                                label="申请时间">
                                <template scope="scope">
                                    <span style="margin-left: 10px">{{scope.row.authingTime|moment}}</span>
                                </template> 
                        </el-table-column>

                    <el-table-column v-if="flag != 'authing'"
                            width="200"
                            label="审核时间">
                        <template scope="scope">
                            <span style="margin-left: 10px">{{scope.row.auditedTime|moment}}</span>
                        </template>
                    </el-table-column>

                    </el-table>
                    <!-- 分页 -->
                    <div class="cs-footer-page">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[10, 20 ,30 ,50]"
                            :page-size="page_size"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="records">
                        </el-pagination>
                    </div>
                </div>
            </div>

            <transition>
                <div class="el-col el-col-24 el-col-xs-24 el-col-md-12 cs-col-right-box" v-if="is_show_rightbox">
                    <smrzxqcomponent ref="smrzxqcomponent" :selectRowData="selectRowData" @reloadList = "reloadList" ></smrzxqcomponent><!--更新 -->
                </div>
            </transition>

        </div>

    </div>
    <imgcomponent></imgcomponent>
</div>
</template>

<script>
export default {
    data() {
        return {
            records:0,
            page_size: 10,
            currentPage:1,
            twoClass: 'el-col-md-12',
            onlyOneClass: 'el-col el-col-24 el-col-xs-24',
            is_show_rightbox: true,
            mianStyle: {},
            mapHeight: {
                type: Number,
                default: 600
            },
            flag:"authing",
            tableData:[],
            keyword:"",
            selectRowData:{deptInfo:{}, authInfo:{}}
        }
    },
    components: {
        smrzxqcomponent : function (resolve, reject) {
            require(['./smrz_xq.vue'],  resolve)
        },
        imgcomponent:function (resolve, reject) {
            require(['./images.vue'],  resolve)
        }
    },
    methods: {
        handleRow(index, row) {
            let _rowdata = row;
            if (_rowdata.idcardImage1 && (_rowdata.idcardImage1.indexOf("http") !=0) ) {
                _rowdata.idcardImage1  = global.config.imgbaseurl + _rowdata.idcardImage1;
            }
            if (_rowdata.idcardImage2 && (_rowdata.idcardImage2.indexOf("http") !=0) ) {
                _rowdata.idcardImage2  = global.config.imgbaseurl + _rowdata.idcardImage2;
            }
            this.selectRowData = _rowdata;
            //this.is_show_rightbox = true;
        },
        reloadList () {
            this.selectRowData = {deptInfo:{},authInfo:{}};
            this.getListData();
        },
        hidden_bjxq() {


        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            this.page_size = val;
            var that = this;
            that.getListData();
        },
        handleCurrentChange(val) {
            var that = this;
            this.currentPage = val;
            that.getListData();
        },
        handleRowHandle(row){
            this.handleRow("", row);
        },
        filterTag(type) {
            let that = this;
            that.flag = type;

            that.getListData();

        },
        search: function () {

            var that = this;
            var res = [];
            that.getListData();
        },

        addListener: function () {
            const that = this;
            const Bus = global.eventHub;
        },
        removeListener: function () {
            const that = this;
            const Bus = global.eventHub;

        },

        resetPage() {
            this.mapHeight = document.documentElement.offsetHeight  - 110;
            this.mianStyle = {
                height:this.mapHeight +"px"
            }
        },
        getListData: function () {
            //console.log("abx")
            let that = this;
            var _param = {
                token:global.data.getToken(),
                page:that.currentPage,
                pageSize:that.page_size
            };
            if (that.keyword) {
                _param.keyword=that.keyword;
            }
            if (that.flag == 'authing') {
                _param.sortby = "authing_time desc";
            } else {
                _param.sortby = "audited_time desc";
            }
            _param.auth = that.flag;
            that.$http.post(global.config.weburl+'api/user/queryUserList.do',_param).then(function(res){
                if(res.data.code==0){
                    var _data= res.data;
                    that.tableData = _data.data;
                    that.records = _data.pageInfo.records;
                    if (that.tableData.length > 0) {
                        that.handleRow(0, that.tableData[0])
                    } else {
                        that.selectRowData = {deptInfo:{},authInfo:{}};
                    }
                } else {
                    global.tools.handleError(res.data);
                }
            },function(error){
                this.$message({
                    type: 'error',
                    message: "获取数据失败"
                });
            });

        }


    },
    created: function () {
        var that = this;
        this.resetPage();
        window.onresize = function(){
            that.resetPage();
        }

        that.getListData();
    },
    mounted: function () {
        const that = this;
        that.addListener();
    },
    beforeDestroy: function () {
        this.removeListener();
    }
}
</script>



